<template>
	<div class="tools">
		<button @click='createNote' class="ui button"> </button>
		<button @click='saveNote' class="ui button"></button>
		<button @click='loadNote' class="ui button"></button>
		<button @click='logUser' class="ui button">{{ renderLogText }}</button>
	</div>
</template>

<script>
	import { mapActions } from 'vuex';

	export default {
		name: 'Tools',
		methods: {
			...mapActions('note', [
				'createNewNote',
				'sendNote',
				'loadList',
				'uploadNoteSession'
			]),
			...mapActions('auth', ['logout', 'login']),
			createNote() {
				this.createNewNote();
			},
			async saveNote() {
				const note = this.$store.getters["note/getSelectedNote"];
				const response = await this.sendNote(note);

				(response.status === 200)
				? alert(' !')
				: alert(response.message);

			},
			async loadNote() {
				this.$emit('loadClick');
			},
			async logUser() {
				if (this.$store.getters["auth/isLoggedIn"]) {
					const cursor_position = document.querySelector('.pad')?.selectionStart;

					this.$store.commit('note/setCursorPositionForSelectedTab', cursor_position);

					const notes = this.$store.state.note.notes;
					const response = await this.uploadNoteSession(notes);

					if (response.status === 200) {
						this.logout();
						this.$store.commit('note/setNotes', []);
					}
				} else {
					this.$store.commit('auth/setModal', true);
				}
			}
		},
		computed: {
			renderLogText() {
				if (this.$store.getters['auth/isLoggedIn']) {
					return '';
				} else {
					return '';
				}
			}
		}
	}
</script>

<style>
	.tools {
		position: absolute;
		left: 10%;
		top: 1%;
	}
</style>